@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 400;
  src: url("../fonts/montserrat-regular.woff") format("woff"), url("../fonts/montserrat-regular.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  src: url("../fonts/montserrat-medium.woff") format("woff"), url("../fonts/montserrat-medium.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 600;
  src: url("../fonts/montserrat-semibold.woff") format("woff"), url("../fonts/montserrat-semibold.woff2") format("woff2");
  font-display: swap;
}
@font-face {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  src: url("../fonts/montserrat-bold.woff") format("woff"), url("../fonts/montserrat-bold.woff2") format("woff2");
  font-display: swap;
}
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  margin: 0;
  padding: 0;
  min-height: 100vh;
}

html {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 24px;
  font-family: "Placeholder", "Arial", sans-serif;
  color: #000000;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  text-rendering: optimizeLegibility;
}

body {
  margin: 0;
  display: flex;
  flex-direction: column;
  min-height: 100%;
  width: 100%;
  font-family: "Montserrat", "Arial", sans-serif;
  background-color: #ffffff;
}

main {
  flex-grow: 1;
}

a {
  color: #000000;
  text-decoration: none;
}

img,
video {
  display: block;
  max-width: 100%;
  height: auto;
}

textarea {
  resize: none;
}

input:-webkit-autofill {
  box-shadow: inset 0 0 0 1000px #ffffff;
  -webkit-text-fill-color: #000000;
}

input,
textarea {
  border-radius: 0;
}
input::placeholder,
textarea::placeholder {
  opacity: 1;
}
input:invalid,
textarea:invalid {
  box-shadow: none;
}

select {
  border-radius: 0;
}

input::-ms-clear, input::-ms-reveal {
  display: none;
}

input[type=search]::-webkit-search-decoration,
input[type=search]::-webkit-search-cancel-button,
input[type=search]::-webkit-search-results-button,
input[type=search]::-webkit-search-results-decoration {
  appearance: none;
}

input::-webkit-outer-spin-button,
input::-webkit-inner-spin-button {
  margin: 0;
  appearance: none;
}

input[type=number] {
  appearance: textfield;
}

select,
textarea,
input:matches([type=email],
[type=number],
[type=password],
[type=search],
[type=tel],
[type=text],
[type=url]) {
  appearance: none;
}

button,
[type=button],
[type=reset],
[type=submit] {
  appearance: none;
}

.wrapper {
  position: relative;
  display: flex;
  flex-direction: column;
  min-height: 100vh;
  /* stylelint-disable-next-line */
  min-height: calc(100 * var(--vh, 1vh));
}
.wrapper .header,
.wrapper .footer {
  flex-shrink: 0;
}
.wrapper main {
  flex-grow: 1;
}

.rouble {
  font-family: "Rouble", "Arial", sans-serif;
  /* stylelint-disable */
  text-transform: lowercase !important;
  font-weight: 400 !important;
  /* stylelint-enable */
}

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  overflow: hidden;
  white-space: nowrap;
  border: 0;
  clip: rect(0 0 0 0);
  clip-path: inset(100%);
}

.scroll-lock-ios {
  position: fixed;
  overflow: hidden;
}

.scroll-lock {
  overflow: hidden;
}

.no-scrollbar {
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.no-scrollbar::-webkit-scrollbar {
  display: none;
}

.no-transition {
  /* stylelint-disable-next-line */
  transition: none !important;
}

.container {
  width: 100%;
  max-width: 1920px;
  margin: 0 auto;
}

.btn {
  display: inline-flex;
  padding: 10px 20px;
  color: #ffffff;
  background-color: #2c39f2;
  border: none;
  cursor: pointer;
  transition: opacity 0.3s ease;
}
.btn--red {
  background-color: #ff1553;
}
.btn:disabled {
  opacity: 0.5;
  pointer-events: none;
}
@media (hover: hover) {
  .btn:hover:not(.focus-visible) {
    opacity: 0.8;
  }
}
.btn.focus-visible:focus {
  opacity: 0.8;
}

.modal {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: 1000;
  display: flex;
  flex-direction: column;
  overflow-y: auto;
  -webkit-overflow-scrolling: touch;
  background-color: rgba(0, 0, 0, 0.8);
  transform: scale(1.2);
  visibility: hidden;
  opacity: 0;
  transition: opacity 0.6s cubic-bezier(0.55, 0, 0.1, 1), transform 0.6s cubic-bezier(0.55, 0, 0.1, 1), visibility 0.6s cubic-bezier(0.55, 0, 0.1, 1);
  pointer-events: none;
  -ms-overflow-style: none;
  scrollbar-width: none;
}
.modal::-webkit-scrollbar {
  display: none;
}
.modal__wrapper {
  position: relative;
  display: flex;
  flex-shrink: 0;
  justify-content: center;
  align-items: center;
  width: 100%;
  min-height: 100%;
  padding: 60px 40px;
}
@media (max-width: 767px) {
  .modal__wrapper {
    padding: 60px 16px;
  }
}
.modal__overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  cursor: pointer;
}
.modal__content {
  position: relative;
  width: 600px;
  padding: 40px;
  background-color: #ffffff;
}
@media (max-width: 767px) {
  .modal__content {
    width: 100%;
    padding: 20px;
  }
}
.modal__close-btn {
  position: absolute;
  top: 22px;
  right: 22px;
  z-index: 1;
  width: 40px;
  height: 40px;
  padding: 0;
  background: rgba(255, 255, 255, 0);
  border: none;
  cursor: pointer;
}
.modal--responsive .modal__content {
  width: 100%;
}
.modal--fit-content .modal__content {
  width: auto;
}
@media (max-width: 767px) {
  .modal--fit-content .modal__content {
    width: 100%;
  }
}
.modal--no-scale {
  transform: none;
}
.modal--preload {
  transition: none;
}
.modal.is-active {
  transform: scale(1);
  visibility: visible;
  opacity: 1;
  pointer-events: auto;
}

.header {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  min-width: 320px;
}

.header__wrapper {
  padding: 35px 32px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  max-width: 1224px;
}
@media (max-width: 1023px) {
  .header__wrapper {
    padding: 30px 45px 15px;
  }
}
@media (max-width: 767px) {
  .header__wrapper {
    padding: 20px 15px;
  }
}

@media (max-width: 767px) {
  .header__wrapper.is-nojs,
.header__wrapper.is-opened {
    position: absolute;
    top: 0;
    right: 0;
    padding-bottom: 34px;
    background-color: #F9FBFD;
    width: 89%;
    min-height: 76px;
  }
}
.nav__toggle {
  display: none;
}

.nav__list {
  margin: 0 0 0 40px;
  padding: 0;
  display: flex;
  flex-wrap: wrap;
  list-style: none;
  font-weight: 500;
  font-size: 16px;
  line-height: 20px;
  align-items: center;
  column-gap: 33px;
  row-gap: 10px;
}
@media (max-width: 1023px) {
  .nav__list {
    font-size: 14px;
    line-height: 17px;
  }
}

.nav__link {
  color: #F9FBFD;
}

.nav--modified .nav__list {
  max-width: 720px;
}
@media (max-width: 1023px) {
  .nav--modified .nav__list {
    font-size: 12px;
    line-height: 15px;
    max-width: 600px;
    column-gap: 23px;
  }
}
@media (max-width: 767px) {
  .nav--modified .nav__list {
    margin: 0;
    flex-direction: column;
    align-items: flex-start;
    row-gap: 23px;
    max-width: none;
  }
}

.nav--modified .nav__item:nth-child(5n) {
  margin-right: 0px;
}

@media (max-width: 767px) {
  .nav .nav__toggle {
    display: block;
  }
  .nav .nav__toggle {
    position: absolute;
    top: 0;
    right: 0;
    width: 24px;
    height: 22px;
    border: none;
    background-color: transparent;
  }
  .nav.is-closed .nav__toggle::before {
    content: "";
    position: absolute;
    top: 25px;
    right: 15px;
    width: 24px;
    height: 2px;
    background: #F9FBFD;
    box-shadow: 0 7px 0 0 #F9FBFD, 0 14px 0 0 #F9FBFD;
    cursor: pointer;
    z-index: 2;
  }
  .nav.is-closed .nav__list {
    display: none;
  }
  .nav.is-opened .nav__toggle::after, .nav.is-opened .nav__toggle::before {
    content: "";
    position: absolute;
    top: 25px;
    right: 15px;
    width: 13px;
    height: 2px;
    background: #011C40;
    cursor: pointer;
    z-index: 2;
  }
  .nav.is-opened .nav__toggle::after {
    transform: rotate(45deg);
  }
  .nav.is-opened .nav__toggle::before {
    transform: rotate(-45deg);
  }
  .nav.is-nojs .nav__list,
.nav.is-opened .nav__list {
    margin: 0;
    padding-left: 15px;
    position: absolute;
    top: 75px;
    left: 0;
    right: 0;
    display: flex;
    flex-direction: column;
    background-color: #F9FBFD;
    align-items: flex-start;
    font-size: 18px;
    line-height: 22px;
    min-height: 87vh;
  }
  .nav.is-nojs .nav__list {
    min-height: auto;
  }
  .nav.is-nojs .nav__item,
.nav.is-opened .nav__item {
    padding: 10px 10px 8px;
    margin-bottom: 10px;
  }
  .nav.is-nojs .nav__link,
.nav.is-opened .nav__link {
    color: #011C40;
  }
  .nav.is-nojs .header__wrapper,
.nav.is-opened .header__wrapper {
    background-color: #F9FBFD;
  }
  .nav.is-nojs .nav__toggle::after, .nav.is-nojs .nav__toggle::before {
    display: none;
  }
  .nav.is-nojs .nav__toggle::after, .nav.is-nojs .nav__toggle::before {
    display: none;
  }
}
.logo {
  width: 70px;
  height: 38px;
}
@media (max-width: 1023px) {
  .logo {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo {
    width: 42px;
    height: 22px;
  }
}

.logo__icon {
  fill: #F9FBFD;
}
@media (max-width: 1023px) {
  .logo__icon {
    width: 38px;
    height: 20px;
  }
}
@media (max-width: 767px) {
  .logo__icon {
    width: 42px;
    height: 22px;
  }
}

@media (max-width: 767px) {
