.logo__icon.is-opened {
    fill: #011C40;
  }
}
.main-block {
  background-color: #011C40;
  background-image: url("../img/main-block.webp");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
  z-index: 2;
  color: #F9FBFD;
  min-width: 320px;
}
