  .main-block__wrapper {
    padding: 280px 45px;
  }
}
@media (max-width: 767px) {
  .main-block__wrapper {
    padding: 185px 15px;
    min-height: 500px;
    max-height: 500px;
  }
}

.main-block__title {
  margin: 0 0 30px 0;
  font-weight: 700;
  font-size: 54px;
  line-height: 66px;
  text-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
}
@media (max-width: 1023px) {
  .main-block__title {
    font-size: 50px;
    line-height: 50px;
  }
}
@media (max-width: 767px) {
  .main-block__title {
    font-size: 36px;
    line-height: 40px;
    margin-bottom: 20px;
  }
}

.main-block__description {
  font-weight: 400;
